<template>
    <div style="text-align:left;font-size:125%;">
        <h2>Method 1: Use <span class="file-name">script</span> to Include the Tableau JS API</h2>
        <br/>
        In the <span class="file-name">public/index.html</span>, add a <span class="file-name">script</span> tag referencing Tableau JS API.
        <br />
    <figure>
    <pre>
        <code class="code-block">
        &lt;head&gt;
        ...
            &lt;script src="https://public.tableau.com/javascripts/api/tableau-2.min.js"&gt;&lt;/script&gt;
        &lt;/head&gt;
        </code>  
    </pre>
    </figure>
            Now, you can use Tableau JS. For example, in the <span class="file-name">src/components/HelloTableau.vue</span>, the code may look like:
            <br />

    <figure>
    <pre>
        <code class="code-block">
        &lt;template&gt;
        &lt;div class="container" style="overflow: auto;"&gt;
            &lt;div ref="vizContainer" :style="'margin:0 auto; width: '+ width + 'px'"&gt;&lt;/div&gt;
        &lt;/div&gt;
        &lt;/template&gt;

        &lt;script&gt;
        export default {
            name: 'HelloTableau',
            data: () => ({
                width: '850',
                height: '700',
                vizUrl: "http://public.tableau.com/views/RegionalSampleWorkbook/Storms",
            }),
            methods:{
                initTableau() {
                    const options = {
                    width: this.width +'px',
                    height: this.height +'px',
                    hideTabs: true,
                    hideToolbar: true,
                    onFirstInteractive: () => {}
                    };
                    this.viz = new window.tableau.Viz(this.$refs.vizContainer, this.vizUrl, options);
                }
            },
            mounted() {
                window.addEventListener('load', () => {
                this.initTableau();
                })
            },
        }
        &lt;/script&gt;
        </code>  
    </pre>
    </figure>

        <h2>Pros &amp; Cons</h2>
        <br/>
        <h3>Pros</h3>
        <ul class="py-5">
            <li>Using the official JS API maintained by the Tableau company.</li>
            <li>Can access the full function set provided by Tableau JS API.</li>
        </ul>
        <h3>Cons</h3>
        <ul class="py-5">
            <li>Tableau JS is always loaded even though most Vue components do not use it.</li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'TableauMethod1'
}
</script>