<template>
    <div style="text-align:left;font-size:125%;">
        <h2>Method 2: Use NPM <span class="file-name">tableau-api</span> Package Created by cmToomey</h2>
        <br/>
        NPM <span class="file-name">tableau-api</span> package is the Tableau JS API wrapped as an NPM module.
        For detail, please reference <a href="https://github.com/cmtoomey/tableau-api" target="_blank">github.com/cmtoomey/tableau-api</a>.
        <br/>
        <br/>
        In the terminal, <span class="file-name">cd</span> to your Vue project folder, install the <span class="file-name">tableau-api</span> package.
        <br/>
<figure>
  <pre>
    <code class="code-block black-bg" >
      &gt; cd  [vue_app_folder]
      &gt; npm install --save tableau-api
    </code>  
  </pre>
</figure>
        Now, you can import <span class="file-name">tableau-api</span> to your Vue components. 
        For example, in the <span class="file-name">src/components/HelloTableau.vue</span>, the code may look like the following:
        <br />
        (If you import <span class="file-name">tableau-api</span> on the <span class="file-name">main.js</span> file, then you can use it anywhere in the Vue app; 
        however, import the api only when it is needed is more preferable.)
        <br />

<figure>
  <pre>
    <code class="code-block">
    &lt;template&gt;
      &lt;div class="container" style="overflow: auto;"&gt;
        &lt;div ref="vizContainer" :style="'margin:0 auto; width: '+ width + 'px'"&gt;&lt;/div&gt;
      &lt;/div&gt;
    &lt;/template&gt;

    &lt;script&gt;
    <span class="highlight py-0">
    import Vue from 'vue';
    import tableau from "tableau-api";
    Vue.use(tableau);
    </span>
    export default {
        name: 'HelloTableau',
        data: () => ({
            width: '850',
            height: '700',
            vizUrl: "http://public.tableau.com/views/RegionalSampleWorkbook/Storms",
        }),
        methods:{
            initTableau() {
              const options = {
                width: this.width +'px',
                height: this.height +'px',
                hideTabs: true,
                hideToolbar: true,
                onFirstInteractive: () => {}
              };
              this.viz = new window.tableau.Viz(this.$refs.vizContainer, this.vizUrl, options);
            }
        },
        mounted() {
          window.addEventListener('load', () => {
            this.initTableau();
          })
        },
    }
    &lt;/script&gt;
    </code>  
  </pre>
</figure>

      <h2>Pros &amp; Cons</h2>
      <br/>
      <h3>Pros</h3>
      <ul class="py-5">
          <li>Tableau JS can be loaded when it is needed.</li>
          <li>Can access the full function set provided by Tableau JS API.</li>
      </ul>
      <h3>Cons</h3>
      <ul class="py-5">
          <li>Not using the latest version of the Tableau JS API</li>
          <li>Not maintained by the Tableau company.</li>
      </ul>
    </div>
</template>

<script>
export default {
    name: 'TableauMethod2'
}
</script>
