<template>
  <v-container>
      <v-row class="text-center">
        <v-col cols="12">
            <h1 class="display-2 pt-5">Embed Tableau in a Vue App</h1>
            <div class="pt-5">
              <div class="container" style="overflow: auto;">
                    <div ref="vizContainer" :style="'margin:0 auto; width: '+ width + 'px'"></div>
              </div>
            </div>
        </v-col>
      </v-row>
      <v-row class="text-center">
        <v-col cols="8" style="margin:0 auto;">
          <h3 class="py-5">The following code examples were developed on a Windows environment.</h3>
      <v-toolbar color="blue"  tabs height="10">
        <template v-slot:extension>
            <v-tabs v-model="tab" color="amber" grow>
              <v-tabs-slider color="cyan"></v-tabs-slider>
              <v-tab v-for="item in items" :key="item">{{ item }}</v-tab>
            </v-tabs>
        </template>
      </v-toolbar>
      <v-tabs-items v-model="tab">
        <v-tab-item   key="Method 1">
          <v-card flat><v-card-text> <TableauMethod1 /> </v-card-text></v-card>
        </v-tab-item>
        <v-tab-item  key="Method 2">
          <v-card flat><v-card-text> <TableauMethod2 /> </v-card-text></v-card>
        </v-tab-item>
        <v-tab-item  key="Method 3">
          <v-card flat><v-card-text> <TableauMethod3 /> </v-card-text></v-card>
        </v-tab-item>
      </v-tabs-items>
        </v-col>
      </v-row>
  </v-container>
</template>
<script>
import TableauMethod1 from '@/components/TableauMethod1.vue';
import TableauMethod2 from '@/components/TableauMethod2.vue';
import TableauMethod3 from '@/components/TableauMethod3.vue';

export default {
  name: 'Tableau',
  components: {
    TableauMethod1,
    TableauMethod2,
    TableauMethod3
  },

  data: () => ({
    width: '850',
    height: '700',
    vizUrl: "http://public.tableau.com/views/RegionalSampleWorkbook/Storms",
    tab: null,
    items: ['Method 1', 'Method 2', 'Method 3'],
    text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
  }),
  methods:{
    initTableau() {
      // // Parameters for Embed Code 
      // // https://help.tableau.com/current/pro/desktop/en-us/embed_list.htm
      // this.vizUrl += "?:embed=yes&:tabs=yes&:showShareOptions=true&:subscriptions=yes&:toolbar=yes&:display_count=yes&:showVizHome=no";
      const options = {
        width: this.width +'px',
        height: this.height +'px',
        hideTabs: true,
        hideToolbar: true,
         onFirstInteractive: () => { 
           // // Add your code to apply Tableau filters or parameters
         }
      };
      this.viz = new window.tableau.Viz(this.$refs.vizContainer, this.vizUrl, options);
    },
    encode( codeStr ) {
         return codeStr.replace(/</gim, '&lt;').replace(/>/gim, '&gt;');
    }
  },
  created(){
    alert( this.encode(this.code))
this.$refs.code1 = this.encode(this.code);

  },
  mounted() {
    window.addEventListener('load', () => {
    this.initTableau();
    })
  }

}
</script>
<style>
.code-block {display:block; color:grey !important; background-color:#f6f6f6 !important;}
.black-bg { color:gainsboro !important; background-color: black !important;}
.file-name {font-family:'Lucida Console'; color: black;}
.highlight{ color:green;}
</style>