<template>
    <div style="text-align:left;font-size:125%;">
        <h2>Method 3: Use NPM <span class="file-name">vue-tableau</span> Package Created by JerryXie98</h2>
        <br/>
        <span class="file-name">vue-tableau</span> is a Vue component integrated with Tableau JS API.
        For detail, please reference <a href="https://github.com/JerryXie98/vue-tableau" target="_blank">github.com/JerryXie98/vue-tableau</a>.
        <br/>
        <br/>
        In the terminal, <span class="file-name">cd</span> to your Vue project folder, 
        install the <span class="file-name">vue-tableau</span> package.
        <br />
<figure>
  <pre>
    <code class="code-block black-bg" >
      &gt; cd  [vue_app_folder]
      &gt; npm install --save vue-tableau
    </code>  
  </pre>
</figure>
        Now, you can import <span class="file-name">vue-tableau</span> to your Vue components. 
        For example, in the <span class="file-name">src/components/HelloTableau.vue</span> may look like the following:
        <br />

<figure>
  <pre>
    <code class="code-block">
        &lt;template&gt;
          &lt;div class="container" style="overflow: auto;"&gt;
            &lt;Tableau  :url="vizUrl" 
                      :width="parseInt(width)"
                      :height="parseInt(height)" 
                      :filters= "{'Storm Name': ['CELIA', 'BLAS', 'AGATHA'],'Basin': 'East Pacific'}"
                      :style="'margin:0 auto; width: '+ width + 'px'"
                      ref = "tableau"    
            &gt;
            &lt;/Tableau&gt;
          &lt;/div&gt;
        &lt;/template&gt;

        &lt;script&gt;
        <span class="highlight">
        import  "vue-tableau";
        </span>
        export default {
            name: 'HelloTableau',
            data: () => ({
                width: '850',
                height: '700',
                vizUrl: "http://public.tableau.com/views/RegionalSampleWorkbook/Storms",
            })
        }
        &lt;/script&gt;
    </code>  
  </pre>
</figure>

      <h2>Pros &amp; Cons</h2>
      <br/>
      <h3>Pros</h3>
      <ul class="py-5">
          <li>Tableau JS can be loaded when it is needed.</li>
          <li>Tableau is used as a Vue component.</li>
      </ul>
      <h3>Cons</h3>
      <ul class="py-5">
          <li>Not using the latest version of the Tableau JS API</li>
          <li>Not maintained by the Tableau company.</li>
          <li>Cannot access the full function set provided by Tableau JS API.
            However, advanced users may modify/coustomize <span class="file-name">vue-tableau</span> for the needed funtions. 
            For example, pre-set a parameter value instead of a filter value.</li>
      </ul>
    </div>
</template>

<script>
export default {
    name: 'TableauMethod3'
}
</script>
